// Dependencies
import React, { useEffect, useState, useContext } from "react";

import cn from 'classnames'
import jwt from 'jsonwebtoken'

// Styles
import "./tailwind.output.css";

const LS_TOKEN = 'api-token'
const A_CLASS = "text-green-400 mr-5 underline";
const TEXT_INPUT_STYLE = "bg-gray-50 border-2 rounded-lg px-2 py-1 mr-2 block w-10/12 text-sm mb-2"

const BUTTON_GREEN =
  "bg-green-400 rounded-lg px-2 py-1 font-semibold text-white block mt-2";

let BASE_URL

switch (window.location.host) {
  case 'localhost:3000':
  case 'localhost:8000':
  case '127.0.0.1:8000':
    BASE_URL = 'http://localhost:8000/v1'
    break;
  case 'episodes-fm-stage.herokuapp.com':
    BASE_URL = 'https://episodes-fm-stage.herokuapp.com/v1'
    break;
  default:
    BASE_URL = 'https://api.episodes.fm/v1'
    break;
}

const APICommand = ({ title, details = '', children, url, response = '' }) => (
  <div className="flex flex-row mb-4">
    <div className="input w-3/6">
      {title && <h3 className="text-lg text-green-400">{title}</h3>}
      {details && <p className="text-sm mb-3">{details}</p>}
      {children}
    </div>
    {url &&
      (<div className="output w-3/6 bg-gray-700 p-3">
        <div className="url text-green-400 font-semibold border-b pb-2 border-gray-600 border-dotted select-all">{url}</div>
        <div style={{ maxHeight: '13rem' }} className="response max-h-50 text-sm overflow-auto overscroll-auto max-w-full"><pre className="text-white text-xs">{response}</pre></div>
      </div>)
    }
  </div>
);

const SubmitButton = ({ label }) => {
  const { headers } = useContext(APIContext)
  return <input type='submit' value={label} className={`${BUTTON_GREEN} disabled:opacity-50`} disabled={!headers} />
}

const GetEpisodeWithShareURLCommand = () => {
  const { headers } = useContext(APIContext);
  const [shareURL, setShareURL] = useState("https://podcasts.apple.com/us/podcast/sean-penn/id1345682353?i=1000488342201");
  const [response, setResponse] = useState("");
  return (
    <APICommand
      title="Get Episode with Share URL"
      details="Provides episode, podcast details & third-party URLs"
      url={`GET /episode/lookup/url/${shareURL || "SHARE_URL"}`}
      response={response}
    >
      <form onSubmit={async (e) => {
        e.preventDefault()
        const apiResponse = await fetch(`${BASE_URL}/episode/lookup/url/${shareURL}`, {
          headers
        })
        try {
          const json = await apiResponse.json()
          setResponse(JSON.stringify(json, null, 2))

        } catch (e) {
          console.error(e)
          setResponse("Error while accessing API")
        }

      }}>
        <input
          className={TEXT_INPUT_STYLE}
          type="text"
          value={shareURL}
          onChange={(e) => setShareURL(e.target.value)}
          placeholder="Share URL"
        />
        <SubmitButton label="Get Episode" />
      </form>
    </APICommand>
  );
};

