const SetAuthToken = () => {
  const { token, setToken } = useContext(APIContext)

  return (
    <APICommand details="API calls require a Authorization: Bearer in the request header.">
      <input type='text' className={TEXT_INPUT_STYLE} value={token} onChange={e => setToken(e.target.value)} placeholder="Authorization Token" />
    </APICommand>
  )
}

const H2 = ({ children, name }) => (
  <h2 id={name} className="text-2xl font-bold mt-5">
    {children}
  </h2>
);



const Form = () => {
  const { role } = useContext(APIContext)
  return (
    <div className="min-h-screen p-8">
      <h1 className="text-3xl font-bold text-green-400">
        Episode.fm API documentation
      </h1>
      <div className='my-5'>
        <p>Calls should be made against <b className='text-green-400'>{BASE_URL}</b>.</p>
        <p className='mb-5'>Authentication is required for all calls, using the method listed below.</p>
        <p>For assistance, please email <a className='text-green-400' href='mailto:hello@episodes.fm'>hello@episodes.fm</a></p>
      </div>
      <div>
        <a className={A_CLASS} href="#authentication">
          Authentication
        </a>
        <a className={A_CLASS} href="#episodes">
          Episodes
        </a>
        <a className={A_CLASS} href="#platforms">
          Platforms
        </a>
        <a className={A_CLASS} href="#podcasts">
          Podcasts
        </a>
        <a className={cn('text-green-400', 'mr-5', 'underline', { 'inline': role === 'admin', 'hidden': role !== 'admin' })} href="#users">
          Users
        </a>
      </div>

      <H2 name="authentication">Authentication</H2>
      <SetAuthToken />
      <hr className="my-10" />

      <H2 name="episode">Episodes</H2>
      <GetEpisodeWithShareURLCommand />
      <GetEpisodeWithFeedAndGUID />
      <hr className="my-10" />

      <H2 name="platforms">Platforms</H2>
      <GetAllPlatforms />
      <hr className="my-10" />

      <H2 name="podcasts">Podcasts</H2>
      <GetPodcastWithFeedURLCommand />
      <hr className="my-10" />

      <div className={cn({ 'block': role === 'admin', 'hidden': role !== 'admin' })}>
        <H2 name="users">Users</H2>
        <GetAllUsers />
        <CreateUser />
        <DeleteUser />
      </div>
    </div>
  );
};

const App = () => {
  const storedHeader = window.localStorage.getItem(LS_TOKEN) ? window.localStorage.getItem(LS_TOKEN) : ""
  const [headers, setHeaders] = useState(storedHeader ? { 'Authorization': `Bearer ${storedHeader}` } : "")
  const [token, setToken] = useState(storedHeader)
  const [role, setRole] = useState("user")

  const apiContext = {
    role,
    setRole,
    token,
    setToken,
    headers
  }

  useEffect(() => {
    if (token) {
      window.localStorage.setItem(LS_TOKEN, token)
      setHeaders({ 'Authorization': `Bearer ${token}` })

      const payload = jwt.decode(token)
      setRole(payload?.role || "user")

    } else {
      window.localStorage.removeItem(LS_TOKEN)
      setHeaders("")
      setRole("user")
    }
  }, [token])

  useEffect(() => {
    const storedToken = window.localStorage.getItem(LS_TOKEN)
    if (storedToken) {
      setToken(storedToken)
    }
  }, [])

  return (<APIContext.Provider value={apiContext}>
    <Form />
  </APIContext.Provider>)
}


const APIContext = React.createContext({
  token: window.localStorage.getItem(LS_TOKEN),
  setToken: () => console.warn('Called before implemented.')
})


export default App;
